.medication-info {
    border-collapse: collapse;
    margin: 25px 0;
    font-size: 0.9em;
    min-width: 100px;
    overflow: hidden;
    border-radius: 5px 5px 0 0;
    box-shadow: 0 0 20px rgba(0,0,0,0.15);

    position:absolute;
    top:100px;
    left:200px;

    white-space: nowrap;
}

.medication-info thead tr {
    background-color: royalblue;
    color: white;
    text-align: left;
    font-weight: bold;
}

.medication-info th, td {
    padding: 12px 15px;
}

.medication-info tbody tr {
    border-bottom: 1px solid lightgray;
}

.medication-info tr td, th {
    border-right: 1px solid lightgray;
}

.medication-info tr td:nth-of-type(1), th:nth-of-type(1) {
    border-right: none;
}

.medication-info tbody tr:last-of-type {
    border-bottom: 2px solid royalblue;
}

.medication-info tbody tr:hover {
    background-color: #f3f3f3;
}

.add-meds {
    position: absolute;
    top: 80px;
    left: 200px;
    width: 100px;
    height: 40px;
    border:none;
    border-radius:5px;
    background-color: #002c80;
    font-size: 0.9em;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.add-meds:active {
    background-color: #002366;
} 

